.aboutContainer {
  display: flex;
  justify-content: center;
  margin-top: 100px;
  height: 750px;
  align-items: center;
  width: 100%;
}

.imageTeaser {
  width: 800px;
  height: 600px;
}

.aboutTitle {
  margin: 0;
  margin-bottom: 20px;
}

.aboutTitle::after {
  display: flex;
  content: "";
  background: linear-gradient(to right, #5ca9fb 0%, #6372ff 100%);
  height: 5px;
  width: 60px;
}

.aboutContent {
  margin-left: 100px;
}

.aboutContent p {
  margin: 0;
  max-width: 600px;
  font-size: 20px;
  margin-bottom: 20px;
  text-align: justify;
}

.aboutListContainer h3 {
  margin: 0;
  display: flex;
  align-self: flex-start;
  margin-bottom: 20px;
  flex-direction: column;
}

.aboutList {
  display: flex;
  justify-content: space-evenly;
}

.whyChooseList {
  list-style: none;
}

.whyChooseList li {
  font-size: 20px;
  margin-top: 5px;
}

.aboutIcon {
  margin-right: 5px;
  color: #6372ff;
}

@media (max-width: 1550px) {
